import "./scss/style.scss";
// Anime.js
import Anime from "./assets/Anime";
// tailwind
import "./App.css";

// AOS
import AOS from "aos";
import "aos/dist/aos.css";

import React, { useEffect, useRef, useState } from "react";
import Header from "./components/Header";
import Visual from "./pages/Visual";
import Profile from "./pages/Profile";
import Skills from "./pages/Skills";
import Portfolio from "./pages/Portfolio";
import Vision from "./pages/Vision";
import Life from "./pages/Life";
import Footer from "./components/Footer";
import Gotop from "./components/Gotop";
import MobileMenu from "./components/MobileMenu";

const App = () => {
  // Anime 
  const main = useRef(null);
  //   
  // useRef :  html   
  // useRef :      
  const pos = useRef([]);
  //   ()  section  
  const getPos = () => {
    // 2.  section   
    pos.current = [];
    // 3.   section   .
    const secs = main.current.querySelectorAll(".scroll");
    for (const item of secs) {
      //     
      pos.current.push(item.offsetTop);
    }
    // console.log(pos.current);
    //   
    const winW = window.innerWidth;
    // console.log(winW);
    if (winW > 860) {
      setOpen(false);
    }
  };

  // 6. header      .
  const [page, setPage] = useState(0);
  useEffect(() => {
    // console.log("   : ", page);
    new Anime(window, {
      prop: "scroll",
      value: pos.current[page] - 80,
      duration: 500,
    });
  }, [page]);

  useEffect(() => {
    // aos 
    AOS.init();

    // 1.  section   
    getPos();
    // 4.     getPos()  
    window.addEventListener("resize", getPos);
    window.addEventListener("scroll", getPos);
    // 5.   
    return () => {
      window.removeEventListener("resize", getPos);
      window.removeEventListener("scroll", getPos);
    };
  }, []);

  //    
  const [open, setOpen] = useState(false);

  return (
    <div className="wrap" ref={main}>
      {/*  */}
      <Gotop />
      {/* 7. page  props  */}
      <Header setPage={setPage} setOpen={setOpen} open={open} />
      <MobileMenu setPage={setPage} setOpen={setOpen} open={open} />

      <div className="container">
        <Visual />
        <Profile />
        <Skills />
        <Portfolio />
        <Life />
        <Vision />
      </div>
      <Footer />
    </div>
  );
};

export default App;
