import { ResponsivePie } from "@nivo/pie";

// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.
const Piechart = () => {
  const handle = {
    padClick: (data) => {
      console.log(data);
    },
    legendClick: (data) => {
      console.log(data);
    },
  };
  return (
    <div style={{ width: "400px", height: "300px" }} className="justify-center">
      <ResponsivePie
        /**
         * chart  
         */
        data={[
          { id: "TypeScript", value: 5 },
          { id: "HTML", value: 25 },
          { id: "CSS", value: 25 },
          { id: "Tailwind", value: 15 },
          { id: "JS", value: 20 },
        ]}
        /**
         * chart margin
         */
        margin={{ top: 0, right: 30, bottom: 80, left: 30 }}
        /**
         * chart   
         */
        innerRadius={0.7}
        /**
         * pad 
         */
        padAngle={1.8}
        /**
         * pad radius  (pad    )
         */
        cornerRadius={8}
        /**
         * chart 
         */
        colors={["#34d399", "#5eead4", "#14b8a6", "#86efac", "#0f766e"]} //   
        // colors={{ scheme: 'nivo' }} // nivo     
        /**
         * pad border  
         */
        borderWidth={0}
        /**
         * link label skip  
         */
        arcLinkLabelsSkipAngle={0}
        /**
         * link label 
         */
        arcLinkLabelsTextColor="none"
        /**
         * link label   
         */
        arcLinkLabelsThickness={0}
        /**
         * link label   
         */
        arcLinkLabelsColor={"none"} // pad  
        /**
         * label (pad  ) skip  
         */
        arcLabelsSkipAngle={10}
        theme={{
          /**
           * label style (pad  )
           */
          labels: {
            text: {
              fontSize: 0,
            },
          },
        }}
        /**
         * pad  
         */
        onClick={handle.padClick}
        /**
         * legend  (default    key )
         */
        legends={[
          {
            anchor: "bottom-right",
            direction: "row",
            justify: false,
            translateX: 70,
            translateY: 56,
            itemsSpacing: -20,
            itemWidth: 100,
            itemHeight: 18,
            itemTextColor: "#999",
            itemDirection: "left-to-right",
            itemOpacity: 1,
            symbolSize: 17,
            symbolShape: "circle",
            effects: [
              {
                on: "hover",
                style: {
                  itemTextColor: "#000",
                },
              },
            ],
          },
        ]}
      />
    </div>
  );
};

export default Piechart;
